import { Directive, Input, Renderer2, Injector, OnInit, ElementRef } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Directive({
    selector: '[farris-set-focus]'
})
export class FarrisSetFocusDirective implements OnInit {

    @Input() set focusState(value: boolean) {
        this._state.next(value);
    }
    private selfEl: HTMLElement
    private _state: BehaviorSubject<any> = new BehaviorSubject({});

    constructor(
        private rd: Renderer2,
        private injector: Injector,
        private el: ElementRef
    ) {

    }

    ngOnInit(): void {
        this.selfEl = this.el.nativeElement;
        this._state.subscribe(data => {
            if (data === false && this.selfEl) {
                if (this.selfEl.nodeName.toLowerCase() === 'farris-html-editor') {
                    this.setHtmlEditorFocus();
                } else {
                    this.setFocus();
                }
            }
        })
    }

    /**
     * 
     */
    setFocus() {
        const input = this.selfEl.querySelectorAll('input');
        if (input.length) {
            input[0].focus()
        } else {
            this.selfEl.focus();
        }
    }

    /**
     * html  
     */
    setHtmlEditorFocus() {
        // input
        const ediotrArea = this.selfEl.querySelector('div[contenteditable="true"]');
        //range  
        const range = window.getSelection();
        //range obj
        range.selectAllChildren(ediotrArea);
        //
        range.collapseToEnd();
    }
}