import { AfterViewInit, Directive, ElementRef, Injector, OnDestroy, OnInit } from "@angular/core";
import { FrameContext } from "@farris/devkit";
import { NumberSpinnerComponent } from "@farris/ui-number-spinner";

@Directive({
  selector: '[numberSpinnerRef]'
})
export class NumberSpinnerComponentRefDirective implements OnInit, OnDestroy, AfterViewInit {
  constructor(private injector: Injector, private component: NumberSpinnerComponent, private elementRef: ElementRef, private frameContext: FrameContext) { }
  ngOnInit(): void {
    const id = this.elementRef && this.elementRef.nativeElement && this.elementRef.nativeElement.id;
    if (this.frameContext && id) {
      this.frameContext.componentRefManager.add(id, this.component);
    }
  }
  ngOnDestroy(): void {
  }
  ngAfterViewInit(): void {
  }
}