import { Directive, ContentChildren, QueryList, Input, NgZone } from '@angular/core';
import { BindingData, BindingList } from '@farris/devkit';
import { ColumnComponent } from '@progress/kendo-angular-grid';

@Directive({
  selector: '[aggregate]',
})
export class GridColumnAggregateDirective {

  @Input() aggregate: string | boolean;

  public get field(): string {
    return this.column.field;
  }

  private get aggrType(): string {
    let type = 'none';
    if (typeof (this.aggregate) === 'boolean') {
      // suminputsum
      if (this.aggregate) {
        type = 'sum';
      }
    } else if (typeof (this.aggregate) === 'string') {
      type = this.aggregate;
    }
    return type;
  }

  constructor(private column: ColumnComponent, private ngZone: NgZone) {
  }

  public recalculateSummary(bindingList: BindingList) {
    let summary = null;
    if (this.aggrType === 'sum') {
      const plainData = bindingList.toJSON().map(item => {
        // udtfielda.b
        return this.field.split('.').reduce((prevPath, currPath) => {
          return prevPath[currPath];
        }, item);
      });
      if (plainData && plainData.length) {
        summary = plainData.reduce((prev, curr) => {
          return prev + curr;
        });
      }
    } else if (this.aggrType === 'count') {
      // lengthtotal
      summary = bindingList.length;
    }
    this.updateFooter(summary);
  }

  public updateFooter(content: any) {
    if (this.aggregate) {
        // setTimeout
        // 
        // todo: settimeoutangulargridbinding
        // setTimeout(() => {
          this.column['summary'] = content;
        // });
    }
  }
}


@Directive({
  selector: '[farrisSummary]'
})
export class GridSummaryDirective/* implements OnInit, AfterViewInit*/ {
  @Input('farrisSummary') summary: string;
  constructor(private bindingData: BindingData) { }

}
