import { Directive, ElementRef, Injector, OnDestroy, OnInit } from "@angular/core";
import { FrameContext } from "@farris/devkit";
import { ViewChangeComponent } from '@farris/ui-view-change';

@Directive({
  selector: '[viewChangeRef]'
})
export class ViewChangeComponentRefDirective implements OnInit, OnDestroy {
  constructor(private injector: Injector, private component: ViewChangeComponent, private elementRef: ElementRef, private frameContext: FrameContext) {

  }
  ngOnInit(): void {
    const id = this.elementRef && this.elementRef.nativeElement && this.elementRef.nativeElement.id;
    if (this.frameContext && id) {
      this.frameContext.componentRefManager.add(id, this.component);
    }
  }
  ngOnDestroy(): void {
  }

}