import { Directive, ElementRef, Injector, OnDestroy, OnInit } from "@angular/core";
import { FrameContext } from "@farris/devkit";
import { ComboListComponent } from '@farris/ui-combo-list';

@Directive({
  selector: '[comboListRef]'
})
export class ComboListComponentRefDirective implements OnInit, OnDestroy {
  constructor(private injector: Injector, private component: ComboListComponent, private elementRef: ElementRef, private frameContext: FrameContext) { }
  ngOnInit(): void {
    const id = this.elementRef && this.elementRef.nativeElement && this.elementRef.nativeElement.id;
    if (this.frameContext && id) {
      this.frameContext.componentRefManager.add(id, this.component);
    }
  }
  ngOnDestroy(): void {
  }

}