import { Directive, Injector, Input, OnInit } from "@angular/core";
import { Change, ChangeType, FrameContext, UIStateChange, UIStateObservableParam, VariableParseService } from "@farris/devkit";
import { PortletComponent } from '@gspwidget/portlet';
import { Observable, Subject } from "rxjs";
import { filter } from "rxjs/operators";
import { ExpressionType, IFilterDependency } from "./types";

const DATA_PATTERN = /\{:?DATA~(\S+?)\}/;
const DATA_PATTERN_GLOBAL = new RegExp(DATA_PATTERN, 'g');
const UI_STATE_PATTERN = /\{:?UISTATE~(\S+?)\}/;
const UI_STATE_PATTERN_GLOBAL = new RegExp(UI_STATE_PATTERN, 'g');

@Directive({
  selector: "[portlet-filter-binding]"
})
export class PortletFilterBindingDirective implements OnInit {
  private subject: Subject<any> = new Subject<any>();
  private readonly FilterFieldKey = 'dpId';
  private readonly FilterValueKey = 'value';

  private get changes(): Observable<any> {
    return this.subject;
  }
  @Input("portlet-filter-binding") filters: string | any[];

  constructor(private injector: Injector, private portletComponent: PortletComponent, private frameContext: FrameContext, private variableParseService: VariableParseService) { }
  public ngOnInit(): void {
    // 
    this.frameContext.bindingData.changes.pipe(filter((change: Change) => {
      return (change.type === ChangeType.Load || change.type === ChangeType.Remove || change.type === ChangeType.Update || change.type === ChangeType.SelectionChanged) && (!change.path || change.path.length < 1);
    })).subscribe((change: Change) => {
      this.filter();
    });
    this.changes.subscribe(() => {
      this.filter();
    });
    this.observeChanges();
  }
  private filter() {
    const filters = this.buildFilters();
    this.portletComponent.widget.handleFilterChange(filters);
  }
  public deserializeFilter() {
    if (!this.filters) {
      return [];
    }
    let filters = [];
    if (typeof this.filters === 'string') {
      filters = JSON.parse(this.filters);
    } else if (Array.isArray(this.filters)) {
      filters = this.filters;
    } else {
      console.error('invalid filters');
    }
    return filters;
  }
  /**
   * 
   * @returns 
   */
  private buildFilters(): any[] {
    const filters = this.deserializeFilter();
    const result = [];
    if (!filters || filters.length < 1) {
      return result;
    }
    filters.forEach(filter => {
      result.push({
        dpId: filter[this.FilterFieldKey],
        value: this.variableParseService.parse(filter[this.FilterValueKey], this.frameContext)
      });
    });
    return result;
  }
  private collectDependencies(expression: string): IFilterDependency[] {
    const bindingDataDependencies = this.getBindingDataDependencies(expression);
    const uistateDependencies = this.getUIstateDependencies(expression);
    return bindingDataDependencies.concat(uistateDependencies);
  }
  private getBindingDataDependencies(expression: string): IFilterDependency[] {
    const matchs = expression.match(DATA_PATTERN_GLOBAL);
    return this.buildDependencies(matchs, DATA_PATTERN, ExpressionType.BindingData);
  }
  private getUIstateDependencies(expression: string): IFilterDependency[] {
    const matchs = expression.match(UI_STATE_PATTERN_GLOBAL);
    return this.buildDependencies(matchs, UI_STATE_PATTERN, ExpressionType.UIState);
  }
  private buildDependencies(expressions: RegExpMatchArray, expr: RegExp, expressionType: ExpressionType) {
    const dependencies: IFilterDependency[] = [];
    expressions && expressions.forEach((expression: string) => {
      const pathMatches = expression.match(expr);
      const dependency = this.buildDependency(pathMatches, expressionType);
      dependencies.push(dependency);
    });
    return dependencies;
  }
  private buildDependency(matchs: RegExpMatchArray, expressionType: ExpressionType) {
    if (matchs && matchs.length === 2) {
      const exps = matchs[1].split('/').filter(p => p);
      const frameId = exps.shift();
      const paths = exps;
      const dependency: IFilterDependency = {
        type: expressionType,
        frameId,
        paths
      };
      return dependency;
    }
  }
  private getFrameContext(frameId: string) {
    frameId = this.frameContext.getFrameId(frameId);
    return this.frameContext.appContext.frameContextManager.getFrameContextById(frameId);
  }
  private observeChanges() {
    const filters = this.deserializeFilter();
    filters && filters.forEach((filter: any) => {
      const dependencies = this.collectDependencies(filter[this.FilterValueKey]);
      if (dependencies && dependencies.length > 0) {
        dependencies.forEach((dep: IFilterDependency) => {
          const frameContext = this.getFrameContext(dep.frameId);
          if (dep.type === ExpressionType.BindingData) {
            this.observeBindingDataChange(frameContext, dep.paths);
          } else if (dep.type === ExpressionType.UIState) {
            this.observeUIStateChange(frameContext, dep.paths.pop());
          }
        })
      }
    });
  }
  private observeUIStateChange(frameContext: FrameContext, path: string) {
    frameContext.uiState.changes.subscribe((change: UIStateObservableParam) => {
      if (change.field === path) {
        this.subject.next();
      }
    });
  }
  private observeBindingDataChange(frameContext: FrameContext, paths: string[]) {
    frameContext.bindingData.changes.pipe(
      // 
      filter((change: Change) => change.type === ChangeType.ValueChanged)
    ).subscribe((change: Change) => {
      if (change.path.join('/') === paths.join('/')) {
        this.subject.next();
      }
    });
  }
}