import { AfterViewInit, Directive, ElementRef, Injector, OnDestroy, OnInit } from "@angular/core";
import { FrameContext } from "@farris/devkit";
import { ListNavComponent } from '@farris/ui-list-nav';

@Directive({
  selector: '[listNavRef]'
})
export class ListNavComponentRefDirective implements OnInit, OnDestroy, AfterViewInit {
  constructor(private injector: Injector, private component: ListNavComponent, private elementRef: ElementRef, private frameContext: FrameContext) {

  }
  //#region angular lifecycle
  ngOnInit(): void {
    const id = this.elementRef && this.elementRef.nativeElement && this.elementRef.nativeElement.id;
    if (this.frameContext && id) {
      this.frameContext.componentRefManager.add(id, this.component);
    }
  }
  ngOnDestroy(): void {
  }
  ngAfterViewInit(): void {
  }
  //#endregion
}