import { Directive, Injector, Input, OnInit, HostListener } from '@angular/core';
import { DiscussionEditorComponent } from '@farris/discussion-group';
import { ViewModel, UIStateObservableParam } from '@farris/devkit';
import { of, Observable, EMPTY } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';
const KEY_UISTATE_REPLY_MESSAGE = 'DISCUSSION_REPLY_MESSAGE_INFO';
const KEY_MESSAGE_ON_COMMENT_ADD = 'onCommentAdd';

@Directive({
  selector: '[farris-discussion-editor-binding]'
})
export class FarrisDiscussionEditorBindingDirective implements OnInit {

  /**
   * 
   */
  @Input("userQueryCommand") userQueryCommand: string;
  /**
   * 
   */
  @Input("addCommentCommand") addCommentCommand: string;
  /**
   * 
   */
  @Input("queryAllOrgsCommand") queryAllOrgsCommand: string;
  /**
   * @
   */
  @Input("queryFrequentAtUsersCommand") queryFrequentAtUsersCommand: string;

  constructor(public injector: Injector, public discussionEditorComponent: DiscussionEditorComponent, public viewModel: ViewModel) {
  }

  ngOnInit(): void {
    this.discussionEditorComponent.replyUser = this.viewModel.frameContext.root.viewModel.uiState[KEY_UISTATE_REPLY_MESSAGE] || {};
    this.viewModel.frameContext.root.viewModel.uiState.changes.subscribe((change: UIStateObservableParam) => {
      if (change.field === KEY_UISTATE_REPLY_MESSAGE) {
        this.discussionEditorComponent.replyUser = this.viewModel.frameContext.root.viewModel.uiState[KEY_UISTATE_REPLY_MESSAGE] || {};
      }
    });
    if (this.queryFrequentAtUsersCommand) {
      this.queryFrequentAtUsers();
    } else {
      this.queryAtUsers();
    }

    this.queryAllOrgs();
  }
  /**
   * 
   */
  private queryAllOrgs() {
    this.execute(this.queryAllOrgsCommand).subscribe(result => {
      if (!result) {
        return;
      }
      this.discussionEditorComponent.sectionData = result;
    });
  }
  /**
   * @
   */
  private queryAtUsers() {
    this.execute(this.userQueryCommand).subscribe(result => {
      if (!result) {
        return;
      }
      const { users = [] } = result;
      this.discussionEditorComponent.personnels = users;
    });
  }
  /**
   * @
   */
  private queryFrequentAtUsers() {
    this.execute(this.queryFrequentAtUsersCommand).subscribe(result => {
      if (!result) {
        return;
      }
      this.discussionEditorComponent.personnels = result && result.users || [];
    });
  }
  /**
   * 
   * @param event event
   */
  @HostListener('valueChange', ['$event'])
  public pageChangedHandler(event: any) {
    const { msgInfo = 0, text = '', visibility = 'ALL', parentId = null } = event || {};
    if (msgInfo === 1) {
      this.execute(this.addCommentCommand, { text, parentId, visibility }).pipe(
        tap(() => {
          this.viewModel.frameContext.appContext.messagePipe.next(KEY_MESSAGE_ON_COMMENT_ADD);
        }),
        catchError(e => EMPTY)
      ).subscribe();
    } else {
      // 
      this.viewModel.frameContext.root.viewModel.uiState[KEY_UISTATE_REPLY_MESSAGE] = {};
      this.discussionEditorComponent.replyUser = {};
    }
  }
  /**
   * 
   * @param commandName 
   */
  private execute(commandName: string, params?: any): Observable<any> {
    if (!commandName || commandName === '' || commandName === 'undefined') {
      return of(null);
    }
    if (typeof params === 'undefined') {
      params = {};
    }
    const paths = commandName.split('.');
    let func = null;
    paths.forEach(path => {
      func = func && func[path] || this[path];
    });
    if (typeof func === 'function') {
      return func(params);
    }
    return of(null);
  }
}
