import { Directive, ElementRef, Injector, OnDestroy, OnInit } from "@angular/core";
import { FrameContext } from "@farris/devkit";
import { HtmlEditorComponent } from '@farris/ui-html-editor';

@Directive({
  selector: '[htmlEditorRef]'
})
export class HtmlEditorComponentRefDirective implements OnInit, OnDestroy {
  constructor(private injector: Injector, private component: HtmlEditorComponent, private elementRef: ElementRef, private frameContext: FrameContext) {

  }
  ngOnInit(): void {
    const id = this.elementRef && this.elementRef.nativeElement && this.elementRef.nativeElement.id;
    if (this.frameContext && id) {
      this.frameContext.componentRefManager.add(id, this.component);
    }
  }
  ngOnDestroy(): void {
  }

}