import { Pipe, PipeTransform } from '@angular/core';
@Pipe({ name: 'formatTotal' })
export class FormatTotalPipe implements PipeTransform {
    transform(value: any, exponent: any): any {
        // 
        if (!value && value !== 0) {
            return '';
        }
        if (exponent.dataType === 'number') {
            // 
            const dataFormat = exponent.format;
            return value.toFixed(Number(dataFormat.substring(1)));
        }
        return value;
    }
}
