import { ControlValueConverter } from '@farris/devkit';

/**
 * 
 */
class MultiLangConverter implements ControlValueConverter {

  /**
   * 
   */
  constructor() {
  }

  /**
   * =>
   */
  public convertFrom(value: string): any {
    const multiLangControlValue = Object.assign({}, value);
    return multiLangControlValue;
  }

  /**
   * =>
   */
  public convertTo(multiLangControlValue: any): any {
    const value = multiLangControlValue;
    return value;
  }

}
export { MultiLangConverter };
