import { Directive, ElementRef, Injector, OnDestroy, OnInit } from "@angular/core";
import { FrameContext } from "@farris/devkit";
import { DiscussionListComponent } from '@farris/discussion-group';

@Directive({
  selector: '[discussionListRef]'
})
export class DiscussionListComponentRefDirective implements OnInit, OnDestroy {
  constructor(private injector: Injector, private component: DiscussionListComponent, private elementRef: ElementRef, private frameContext: FrameContext) { }
  ngOnInit(): void {
    const id = this.elementRef && this.elementRef.nativeElement && this.elementRef.nativeElement.id;
    if (this.frameContext && id) {
      this.frameContext.componentRefManager.add(id, this.component);
    }
  }
  ngOnDestroy(): void {
  }

}